import React from 'react'
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateSubscription } from "../../actions/userActions";
import { withRouter } from "react-router-dom";
import { toast } from 'react-toastify';
import $ from 'jquery';
import axios from "axios";
import 'react-toastify/dist/ReactToastify.css';
import Select from "react-select";
import keys from "../../actions/config";
const url = keys.baseUrl;
class SubscriptionUpdateModal extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            id: this.props.record.id,
            name: this.props.record.name,
            categoryname: this.props.record.categoryname,
            price: this.props.record.price,
            duration:this.props.record.duration,
            features:this.props.record.features,
            errors: {},
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.record) {
            this.setState({
                id: nextProps.record.id,
                name: nextProps.record.name,
                categoryname: nextProps.record.categoryname,
                price:nextProps.record.price,
                duration:nextProps.record.duration,
                features:nextProps.record.features,
            })
        }
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }

        if (nextProps.auth !== undefined
            && nextProps.auth.updatenewuser !== undefined
            && nextProps.auth.updatenewuser.data !== undefined
            && nextProps.auth.updatenewuser.data.message !== undefined
            && nextProps.auth.updatenewuser.data.success) {
            $('#update-user-modal').modal('hide');
            toast(nextProps.auth.updatenewuser.data.message, {
                position: toast.POSITION.TOP_CENTER
            });
            nextProps.auth.updatenewuser = "";
        }
    }
 // getData() {
 //           axios
 //           .get(url+"api/pair-data-first")
 //            .then(res => {  
                
 //               console.log(res.data,'datassss');
 //                var currencyarray =[]; 
 //                  res.data.map((item,i)=>{
 //                    const name = item.currencySymbol;
 //                    const value = item.currencySymbol;         
 //                    const obj = {'value':name, 'label':value};
 //                  currencyarray.push(obj);
 //                   });
 //                 // console.log(currencyarray,'currencyarray');
 //                   this.setState({ first_currency1:currencyarray,email_assigned:true});
 //             })
 //            .catch()
 //      //  console.log(this.props,'authget1');
 //    }
    onChange = e => {
        this.setState({ [e.target.id]: e.target.value });
    };
 handleChange = selectedOption => {
    this.setState({mobileoperatorname : selectedOption.value });
  //  console.log(`Option selected:`, selectedOption );
  };
    onChange = e => {
        if (e.target.id === 'user-update-name') {
            this.setState({ name: e.target.value });
        }
        if (e.target.id === 'user-update-categoryname') {
            this.setState({ categoryname: e.target.value });
        }
        if (e.target.id === 'user-update-price') {
            this.setState({ price: e.target.value });
        }
        if (e.target.id === 'user-update-duration') {
            this.setState({ duration: e.target.value });
        }
        if (e.target.id === 'user-update-features') {
            this.setState({ features: e.target.value });
        }
    };

    onUserUpdate = e => {
        e.preventDefault();
        const newSubscription = {
            _id: this.state.id,
            name: this.state.name,
            categoryname: this.state.categoryname,
            price: this.state.price,
            duration:this.state.duration,
            features:this.state.features,
        };
        this.props.updateSubscription(newSubscription);
    };
    render() {
         const { errors } = this.state;
      
        return (
            <div>
                <div className="modal fade" id="update-user-modal">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Update Subscription </h4>
                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                            </div>
                            <div className="modal-body">
                                <form noValidate onSubmit={this.onUserUpdate} id="update-user">
                                    <input
                                        onChange={this.onChange}
                                        value={this.state.id}
                                        id="user-update-id"
                                        type="text"
                                        className="d-none"/>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="name">Name</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.name}
                                                id="user-update-name"
                                                type="text"
                                                error={errors.name}
                                                className={classnames("form-control", {
                                                    invalid: errors.name
                                                })}/>
                                            <span className="text-danger">{errors.name}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="categoryname">Category name</label>
                                        </div>
                                        <div className="col-md-9">
                                     {/* <Select
                                                value={selectedOption}
                                                onChange={this.handleChange}
                                                options={this.state.mobileoperatorname}
                                            />*/}
                                             <input
                                                onChange={this.onChange}
                                                value={this.state.categoryname}
                                                error={errors.categoryname}
                                                id="user-update-categoryname"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.categoryname
                                                })}
                                            />
                                            <span className="text-danger">{errors.categoryname}</span>
                                        </div>
                                    </div>
                                  <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="price">Price</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.price}
                                                id="user-update-price"
                                                type="text"
                                                error={errors.price}
                                                className={classnames("form-control", {
                                                    invalid: errors.price
                                                })}/>
                                            <span className="text-danger">{errors.price}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="duration">Duration</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.duration}
                                                id="user-update-duration"
                                                type="text"
                                                error={errors.duration}
                                                className={classnames("form-control", {
                                                    invalid: errors.duration
                                                })}/>
                                            <span className="text-danger">{errors.duration}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="features">Features</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.features}
                                                id="user-update-features"
                                                type="text"
                                                error={errors.features}
                                                className={classnames("form-control", {
                                                    invalid: errors.features
                                                })}/>
                                            <span className="text-danger">{errors.features}</span>
                                        </div>
                                    </div>
                                </form>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                <button
                                    form="update-user"
                                    type="submit"
                                    className="btn btn-primary">
                                    Update User
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

SubscriptionUpdateModal.propTypes = {
    updateSubscription: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(
    mapStateToProps,
    { updateSubscription }
)(withRouter(SubscriptionUpdateModal));
