import React from 'react'
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateUser } from "../../actions/userActions";
import { withRouter } from "react-router-dom";
import { toast } from 'react-toastify';
import $ from 'jquery';
import Select from 'react-select';
import axios from "axios";
import 'react-toastify/dist/ReactToastify.css';
import keys from "../../actions/config";
const url = keys.baseUrl;

class UserUpdateModal extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            id: this.props.record.id,
            userName: this.props.record.userName,
            email: this.props.record.email,
            businessPhoneNumber:this.props.record.businessPhoneNumber,
            mobileMoneyPhoneNumber:this.props.record.mobileMoneyPhoneNumber,
            // superMerchant:this.props.record.superMerchant,
            // agencyName:this.props.record.agencyName,
            address:this.props.record.address,
            mapAddress:this.props.record.mapAddress,
            city:this.props.record.city,
            region:this.props.record.region,
            country:this.props.record.country,
            account:this.props.record.account,
            superMerchant         : { label: this.props.record.name, value: this.props.record.name },
            agencyName            : { label: this.props.record.name1, value: this.props.record.name1 },
            errors: {},
        };
         this.handleChange = this.handleChange.bind(this);
         this.handleChange1 = this.handleChange1.bind(this);
    }
    componentDidMount() {
       this.getData();
       var a = '';
       var b = '';
       this.getData1(a);
       this.getData2();
       this.getData3(b);
    }
    handleChange = selectedOption => {
    this.setState({ superMerchant: selectedOption.value });
    //  console.log(`Option selected:`, selectedOption );
    this.getData1(selectedOption.value);
  };
  handleChange1 = selectedOption => {
    this.setState({ agencyName : selectedOption.value });
    //console.log(`Option selected:`, selectedOption );
  };
   handleChange2 = selectedOption => {
    this.setState({ region : selectedOption.value });
     this.getData3(selectedOption.value);
    //console.log(`Option selected:`, selectedOption );
  };
   handleChange3 = selectedOption => {
    this.setState({ city : selectedOption.value });
    //console.log(`Option selected:`, selectedOption );
  };
   getData(){
    axios
    .get(url + "api/superMerchant")
    .then(res=>{
                var superMerchantarray =[]; 
                  res.data.map((item,i)=>{       
                    const obj = {'value':item._id, 'label':item.name};
                    // alert(JSON.stringify(item));
                    // alert(JSON.stringify(obj));
                    superMerchantarray.push(obj);
                    
                });
                
                   this.setState({ name:superMerchantarray});

             })
            .catch()
  }
   getData1(a){
        if(a!=''){
            // alert(a)
            axios
            .get(url + "api/agencyName?supermerchantid="+a)
            .then(res=>{
                        console.log(res.data,'agencyname213');
                        var agencyNamearray =[]; 
                        res.data.map((item,i)=>{
                            const name = item.name;
                            const value = item.name;         
                            const obj = {'value':name, 'label':value};
                            agencyNamearray.push(obj);
                            
                        });
                        
                        this.setState({ name1:agencyNamearray});
                     })
                    .catch()
        }
  }

getData2(){
    axios
    .get(url + "api/region")
    .then(res=>{
                var regionarray =[]; 
                  res.data.map((item,i)=>{       
                    const obj = {'value':item._id, 'label':item.name};
                    // alert(JSON.stringify(item));
                    // alert(JSON.stringify(obj));
                    regionarray.push(obj);
                    
                });
                
                   this.setState({ name2:regionarray});

             })
            .catch()
  }

 getData3(b){
        if(b!=''){
            
            axios
            .get(url + "api/city?regionid="+b)
            .then(res=>{
                        console.log(res.data,'agencyname213');
                        var cityarray =[]; 
                        res.data.map((item,i)=>{
                            const name = item.name;
                            const value = item.name;         
                            const obj = {'value':name, 'label':value};
                            cityarray.push(obj);
                            
                        });
                        
                        this.setState({ name3:cityarray});
                     })
                    .catch()
        }
  }
    componentWillReceiveProps(nextProps) {
        if (nextProps.record) {
            this.setState({
                id: nextProps.record.id,
                userName: nextProps.record.userName,
                businessPhoneNumber:nextProps.record.businessPhoneNumber,
                mobileMoneyPhoneNumber:nextProps.record.mobileMoneyPhoneNumber,
                address:nextProps.record.address,
                mapAddress:nextProps.record.mapAddress,
                city:nextProps.record.city,
                region:nextProps.record.region,
                country:nextProps.record.country,
                account:nextProps.record.account,
                email: nextProps.record.email,
                superMerchant      : {label: nextProps.record.name, value: nextProps.record.name },
                agencyName         : {label: nextProps.record.name1, value: nextProps.record.name1 },
                city               : {label: nextProps.record.name2, value: nextProps.record.name2 },
                region             : {label: nextProps.record.name3, value: nextProps.record.name3 },
            })
        }
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }
        if (nextProps.auth !== undefined
            && nextProps.auth.updatenewuser !== undefined
            && nextProps.auth.updatenewuser.data !== undefined
            && nextProps.auth.updatenewuser.data.message !== undefined
            && nextProps.auth.updatenewuser.data.success) {
            $('#update-user-modal').modal('hide');
            toast(nextProps.auth.updatenewuser.data.message, {
                position: toast.POSITION.TOP_CENTER
            });
            nextProps.auth.updatenewuser = "";
        }
    }

    onChange = e => {
        if (e.target.id === 'user-update-name') {
            this.setState({ userName: e.target.value });
        }
        if (e.target.id === 'user-update-businessPhoneNumber') {
            this.setState({ businessPhoneNumber: e.target.value });
        }
        if (e.target.id === 'user-update-mobileMoneyPhoneNumber') {
            this.setState({ mobileMoneyPhoneNumber: e.target.value });
        }
        if (e.target.id === 'user-update-superMerchant') {
            this.setState({ superMerchant: e.target.value });
        }
        if (e.target.id === 'user-update-agencyName') {
            this.setState({ agencyName: e.target.value });
        }
         if (e.target.id === 'user-update-address') {
            this.setState({ address: e.target.value });
        }
         if (e.target.id === 'user-update-mapAddress') {
            this.setState({ mapAddress: e.target.value });
        }
           if (e.target.id === 'user-update-city') {
            this.setState({ city: e.target.value });
        }
           if (e.target.id === 'user-update-country') {
            this.setState({ country: e.target.value });
        }
          if (e.target.id === 'user-update-address') {
            this.setState({ address: e.target.value });
        }
          if (e.target.id === 'user-update-region') {
            this.setState({ region: e.target.value });
        }
        if (e.target.id === 'user-update-email') {
            this.setState({ email: e.target.value });
        }
        if (e.target.id === 'user-update-account') {
            this.setState({ account: e.target.value });
        }
        if (e.target.id === 'user-update-password') {
            this.setState({ password: e.target.value });
        }
    };

    onUserUpdate = e => {
        e.preventDefault();
        const newUser = {
            _id: this.state.id,
            userName: this.state.userName,
            email: this.state.email,
            businessPhoneNumber:this.state.businessPhoneNumber,
            mobileMoneyPhoneNumber:this.state.mobileMoneyPhoneNumber,
            address:this.state.address,
            mapAddress:this.state.mapAddress,
            country:this.state.country,
            account:this.state.account,
            city:this.state.city,
            region:this.state.region,
            superMerchant:this.state.superMerchant,
            agencyName:this.state.agencyName,
        };
        this.props.updateUser(newUser);
    };
    render() {
          const { errors,selectedOption,selectedOption1 } = this.state;
        return (
            <div>
                <div className="modal fade" id="update-template-modal">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Update User</h4>
                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                            </div>
                            <div className="modal-body">
                                <form noValidate onSubmit={this.onUserUpdate} id="update-user">
                                    <input
                                        onChange={this.onChange}
                                        value={this.state.id}
                                        id="user-update-id"
                                        type="text"
                                        className="d-none"/>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="userName">Name</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.userName}
                                                id="user-update-name"
                                                type="text"
                                                error={errors.userName}
                                                className={classnames("form-control", {
                                                    invalid: errors.userName
                                                })}/>
                                            <span className="text-danger">{errors.userName}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="businessPhoneNumber">businessPhoneNumber</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.businessPhoneNumber}
                                                id="user-update-businessPhoneNumber"
                                                type="text"
                                                error={errors.businessPhoneNumber}
                                                className={classnames("form-control", {
                                                    invalid: errors.businessPhoneNumber
                                                })}/>
                                            <span className="text-danger">{errors.businessPhoneNumber}</span>
                                        </div>
                                    </div>

                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="email">Email</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.email}
                                                error={errors.email}
                                                id="user-update-email"
                                                type="email"
                                                className={classnames("form-control", {
                                                    invalid: errors.email
                                                })}
                                            />
                                            <span className="text-danger">{errors.email}</span>
                                        </div>
                                    </div>
                                  {/*   <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="password">Password</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                data-reset-input={true}
                                                autoComplete={''}
                                                onChange={this.onChange}
                                                error={errors.password}
                                                id="user-update-password"
                                                type="password"
                                                className={classnames("form-control", {
                                                    invalid: errors.password
                                                })}
                                            />
                                            <span className="text-danger">{errors.password}</span>
                                        </div>
                                    </div>*/}

                                      <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="mobileMoneyPhoneNumber">Mobile Money Phone No</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.mobileMoneyPhoneNumber}
                                                error={errors.mobileMoneyPhoneNumber}
                                                id="user-update-mobileMoneyPhoneNumber"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.mobileMoneyPhoneNumber
                                                })}
                                            />
                                            <span className="text-danger">{errors.mobileMoneyPhoneNumber}</span>
                                        </div>
                                    </div>
                                     
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="superMerchant">Super Merchant</label>
                                        </div>
                                        <div className="col-md-9">
                                         {(this.state.superMerchant)?
                                            <Select
                                            id="user-update-superMerchant"
                                             value={this.state.superMerchant}
                                               defaultValue={{label: this.state.superMerchant.label ,value: this.state.superMerchant.label}}
                                                onChange={this.handleChange}
                                                options={this.state.name}
                                            />:''}
                                            
                                           {/*  <input
                                                onChange={this.onChange}
                                                value={this.state.superMerchant}
                                                error={errors.superMerchant}
                                                id="user-update-superMerchant"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.superMerchant
                                                })}
                                            />*/}
                                            <span className="text-danger">{errors.superMerchant}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="agencyName">Agency name</label>
                                        </div>
                                        <div className="col-md-9">
                                        {(this.state.agencyName)?
                                            <Select
                                            id="user-update-agencyName"
                                             value={this.state.agencyName}
                                               defaultValue={{label: this.state.agencyName.label ,value: this.state.agencyName.label}}
                                                onChange={this.handleChange1}
                                                options={this.state.name1}
                                            />:''}
                                        {/*     <input
                                                onChange={this.onChange}
                                                value={this.state.agencyName}
                                                error={errors.agencyName}
                                                id="user-update-agencyName"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.agencyName
                                                })}
                                            />*/}
                                            <span className="text-danger">{errors.superMerchant}</span>
                                        </div>
                                    </div>
                                     <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="address">address</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.address}
                                                error={errors.address}
                                                id="user-update-address"
                                                type="tetx"
                                                className={classnames("form-control", {
                                                    invalid: errors.address
                                                })}
                                            />
                                            <span className="text-danger">{errors.address}</span>
                                        </div>
                                    </div>
                                       <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="address">Map address</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.mapAddress}
                                                error={errors.mapAddress}
                                                id="user-update-mapAddress"
                                                type="tetx"
                                                className={classnames("form-control", {
                                                    invalid: errors.mapAddress
                                                })}
                                            />
                                            <span className="text-danger">{errors.mapAddress}</span>
                                        </div>
                                    </div>
                                   
                                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">City</label>
                                        </div>
                                    <div className="col-md-9">
                                        
                                             {(this.state.city)?
                                            <Select
                                            id="user-update-agencyName"
                                             value={this.state.city}
                                               defaultValue={{label: this.state.city.label ,value: this.state.city.label}}
                                                onChange={this.handleChange2}
                                                options={this.state.name2}
                                            />:''}
                                {/*     <input
                                                onChange={this.onChange}
                                                value={this.state.city}
                                                id="user-update-city"
                                                type="text"
                                                error={errors.city}
                                                className={classnames("form-control", {
                                                    invalid: errors.city
                                                })}/>*/}
                      <span className="text-danger">
                        {errors.city}
                      </span>
                    </div>
                  </div>              
                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Region</label>
                                        </div>
                                    <div className="col-md-9">
                                       {(this.state.region)?
                                            <Select
                                            id="user-update-agencyName"
                                             value={this.state.agencyName}
                                               defaultValue={{label: this.state.agencyName.label ,value: this.state.agencyName.label}}
                                                onChange={this.handleChange3}
                                                options={this.state.name3}
                                            />:''}
                                  {/*  <input
                                                onChange={this.onChange}
                                                value={this.state.region}
                                                id="user-update-region"
                                                type="text"
                                                error={errors.region}
                                                className={classnames("form-control", {
                                                    invalid: errors.region
                                                })}/> */}
                      <span className="text-danger">
                        {errors.region}
                      </span>
                    </div>
                  </div>
                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Country</label>
                                        </div>
                                    <div className="col-md-9">
                                        {/*<Select
                                                value={selectedOption}
                                                onChange={this.handleChange}
                                                options={this.state.account}
                                            /> */}
                                    <input
                                                onChange={this.onChange}
                                                value={this.state.country}
                                                id="user-update-country"
                                                type="text"
                                                error={errors.country}
                                                className={classnames("form-control", {
                                                    invalid: errors.country
                                                })}/>
                      <span className="text-danger">
                        {errors.country}
                      </span>
                    </div>
                  </div>
                                     <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Account</label>
                                        </div>
                                    <div className="col-md-9">
                                        {/*<Select
                                                value={selectedOption}
                                                onChange={this.handleChange}
                                                options={this.state.account}
                                            /> */}
                                    <input
                                                onChange={this.onChange}
                                                value={this.state.account}
                                                id="user-update-account"
                                                type="text"
                                                error={errors.account}
                                                className={classnames("form-control", {
                                                    invalid: errors.account
                                                })}/>
                      <span className="text-danger">
                        {errors.account}
                      </span>
                    </div>
                  </div>
                                </form>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                <button
                                    form="update-user"
                                    type="submit"
                                    className="btn btn-primary">
                                    Update User
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

UserUpdateModal.propTypes = {
    updateUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(
    mapStateToProps,
    { updateUser }
)(withRouter(UserUpdateModal));
