import React from 'react'
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addUser } from "../../actions/userActions";
import { withRouter } from "react-router-dom";
import { toast } from 'react-toastify';
import $ from 'jquery';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";
import Select from "react-select";
import keys from "../../actions/config";
const url = keys.baseUrl;
class UserAddModal extends React.Component {

    constructor() {
        super();
        this.state = {
            userName: "",
            businessPhoneNumber:"",
            email:"",
            mobileMoneyPhoneNumber:"",
            superMerchant:"",
            agencyName:"",
            address:"",
            mapAddress:"",
            city:"",
            region:"",
            country:"",
            account:"",
            errors: {},
            name:[],
            name1:[],
        };
        
    }
    componentDidMount() {
       this.getData();
       var a = '';
       var b = '';
       this.getData1(a);
       this.getData2();
       this.getData3(b);
       this.getData4();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }
        if (nextProps.auth !== undefined
            && nextProps.auth.newuser !== undefined
            && nextProps.auth.newuser.data !== undefined
            && nextProps.auth.newuser.data.message !== undefined) {
            $('#add-user-modal').modal('hide');
            toast(nextProps.auth.newuser.data.message, {
                position: toast.POSITION.TOP_CENTER
            });
            nextProps.auth.newuser = undefined;
        }
    }


  handleChange = selectedOption => {
    this.setState({ superMerchant: selectedOption.value });
    //  console.log(`Option selected:`, selectedOption );
    this.getData1(selectedOption.value);
  };
  handleChange1 = selectedOption => {
    this.setState({ agencyName : selectedOption.value });
    //console.log(`Option selected:`, selectedOption );
  };
   handleChange2 = selectedOption => {
    this.setState({ region : selectedOption.value });
     this.getData3(selectedOption.value);
    //console.log(`Option selected:`, selectedOption );
  };
   handleChange3 = selectedOption => {
    this.setState({ city : selectedOption.value });
    //console.log(`Option selected:`, selectedOption );
  };
  handleChange4 = selectedOption => {
    this.setState({ account : selectedOption.value });
    //console.log(`Option selected:`, selectedOption );
  };
   getData(){
    axios
    .get(url + "api/superMerchant")
    .then(res=>{
         // console.log(res.data,'datassss');
                var superMerchantarray =[]; 
                  res.data.map((item,i)=>{       
                    const obj = {'value':item._id, 'label':item.name};
                    // alert(JSON.stringify(item));
                    // alert(JSON.stringify(obj));
                    superMerchantarray.push(obj);
                    
                });
                
                   this.setState({ name:superMerchantarray});

             })
            .catch()
  }
   getData1(a){
        if(a!=''){
            // alert(a)
            axios
            .get(url + "api/agencyName?supermerchantid="+a)
            .then(res=>{
                        console.log(res.data,'agencyname213');
                        var agencyNamearray =[]; 
                        res.data.map((item,i)=>{
                            const name = item.name;
                            const value = item.name;         
                            const obj = {'value':name, 'label':value};
                            agencyNamearray.push(obj);
                            
                        });
                        
                        this.setState({ name1:agencyNamearray});
                     })
                    .catch()
        }
  }

getData2(){
    axios
    .get(url + "api/region")
    .then(res=>{
         // console.log(res.data,'datassss');
                var regionarray =[]; 
                  res.data.map((item,i)=>{       
                    const obj = {'value':item._id, 'label':item.name};
                    // alert(JSON.stringify(item));
                    // alert(JSON.stringify(obj));
                    regionarray.push(obj);
                    
                });
                
                   this.setState({ name2:regionarray});

             })
            .catch()
  }

 getData3(b){
        if(b!=''){
             
            axios
            .get(url + "api/city?regionid="+b)
            .then(res=>{
                        console.log(res.data,'agencyname213');
                        var cityarray =[]; 
                        res.data.map((item,i)=>{
                            console.log(item)
                            const name = item.name;
                            const value = item.name;         
                            const obj = {'value':name, 'label':value};
                            cityarray.push(obj);
                            
                        });
                        
                        this.setState({ name3:cityarray});
                     })
                    .catch()
        }
  }
   getData4(){
       
            axios
            .get(url + "api/account1")
            .then(res=>{
                        console.log(res.data,'agencyname213');
                        var accountarray =[]; 
                        res.data.map((item,i)=>{
                            const name = item.name;
                            const value = item.name;         
                            const obj = {'value':name, 'label':value};
                            accountarray.push(obj);
                            
                        });
                        
                        this.setState({ name4:accountarray});
                     })
                    .catch()
        
  }

    onChange = e => {
        this.setState({ [e.target.id]: e.target.value });
    };

    onUserAdd = e => {
        e.preventDefault();
        const newUser = {
            userName: this.state.userName,
            businessPhoneNumber:this.state.businessPhoneNumber,
            email: this.state.email,
            mobileMoneyPhoneNumber:this.state.mobileMoneyPhoneNumber,
            superMerchant:this.state.superMerchant,
            agencyName:this.state.agencyName,
            address:this.state.address,
            mapAddress:this.state.mapAddress,
            city:this.state.city,
            region:this.state.region,
            country:this.state.country,
            account:this.state.account,
            
        };
        this.props.addUser(newUser, this.props.history);
    };

    render() {
             const { errors }        = this.state;
             const { superMerchant } = this.state.superMerchant;    
             const { agencyName }    = this.state.agencyName;
             const { region }        = this.state.region;
             const { city }          = this.state.city;
             const { account }       = this.state.account;
        return (
            <div>
                <div className="modal fade" id="add-user-modal" data-reset="true">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Add User</h4>
                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                            </div>
                            <div className="modal-body">
                                <form noValidate onSubmit={this.onUserAdd} id="add-user">
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="userName">Name</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.userName}
                                                id="userName"
                                                type="text"
                                                error={errors.userName}
                                                className={classnames("form-control", {
                                                    invalid: errors.userName
                                                })}/>
                                            <span className="text-danger">{errors.userName}</span>
                                        </div>
                                    </div>
                                    
                               
         {/*           <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="password">Password</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.password}
                                                id="password"
                                                type="text"
                                                error={errors.password}
                                                className={classnames("form-control", {
                                                    invalid: errors.password
                                                })}/>
                                            <span className="text-danger">{errors.password}</span>
                                        </div>
                                    </div>
<div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="password">Confirm Password</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.password2}
                                                id="password2"
                                                type="text"
                                                error={errors.password2}
                                                className={classnames("form-control", {
                                                    invalid: errors.password2
                                                })}/>
                                            <span className="text-danger">{errors.password2}</span>
                                        </div>
                                    </div>*/}
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="businessPhoneNumber">Business Phone No</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.businessPhoneNumber}
                                                error={errors.businessPhoneNumber}
                                                id="businessPhoneNumber"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.businessPhoneNumber
                                                })}
                                            />
                                            <span className="text-danger">{errors.businessPhoneNumber}</span>
                                        </div>
                                    </div>
                                    
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="email">Email</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.email}
                                                id="email"
                                                type="text"
                                                error={errors.email}
                                                className={classnames("form-control", {
                                                    invalid: errors.email
                                                })}/>
                                            <span className="text-danger">{errors.email}</span>
                                        </div>
                                    </div>
                                  
                                                                
                                      <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="mobileMoneyPhoneNumber">Mobile Money Phone No</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.mobileMoneyPhoneNumber}
                                                error={errors.mobileMoneyPhoneNumber}
                                                id="mobileMoneyPhoneNumber"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.mobileMoneyPhoneNumber
                                                })}
                                            />
                                            <span className="text-danger">{errors.mobileMoneyPhoneNumber}</span>
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="superMerchant">Super Merchant</label>
                                        </div>
                                        <div className="col-md-9">
                                        <Select
                                                id="superMerchant"
                                                value={city}
                                                onChange={this.handleChange}
                                                options={this.state.name}

                                            /> 
                                           {/*  <input
                                                onChange={this.onChange}
                                                value={this.state.superMerchant}
                                                error={errors.superMerchant}
                                                id="superMerchant"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.superMerchant
                                                })}
                                            />*/}
                                            <span className="text-danger">{errors.superMerchant}</span>
                                        </div>
                                    </div>
                                     <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="agencyName">Agency name</label>
                                        </div>
                                        <div className="col-md-9">
                                        <Select
                                                id="agencyName"
                                                value={agencyName}
                                                onChange={this.handleChange1}
                                                options={this.state.name1}
                                            /> 
                                           {/*  <input
                                                onChange={this.onChange}
                                                value={this.state.agencyName}
                                                error={errors.agencyName}
                                                id="agencyName"
                                                type="text"
                                                className={classnames("form-control", {
                                                    invalid: errors.agencyName
                                                })}
                                            />*/}
                                            <span className="text-danger">{errors.agencyname}</span>
                                        </div>
                                    </div>
                                     <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="address">address</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.address}
                                                error={errors.address}
                                                id="address"
                                                type="tetx"
                                                className={classnames("form-control", {
                                                    invalid: errors.address
                                                })}
                                            />
                                            <span className="text-danger">{errors.address}</span>
                                        </div>
                                    </div>
                                       <div className="row mt-2">
                                        <div className="col-md-3">
                                            <label htmlFor="address">Map address</label>
                                        </div>
                                        <div className="col-md-9">
                                            <input
                                                onChange={this.onChange}
                                                value={this.state.mapAddress}
                                                error={errors.mapAddress}
                                                id="mapAddress"
                                                type="tetx"
                                                className={classnames("form-control", {
                                                    invalid: errors.mapAddress
                                                })}
                                            />
                                            <span className="text-danger">{errors.mapAddress}</span>
                                        </div>
                                    </div>
                                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Region</label>
                                        </div>
                                    <div className="col-md-9">
                                        <Select
                                                value={region}
                                                onChange={this.handleChange2}
                                                options={this.state.name2}
                                            /> 
                                   {/*  <input
                                                onChange={this.onChange}
                                                value={this.state.region}
                                                id="region"
                                                type="text"
                                                error={errors.region}
                                                className={classnames("form-control", {
                                                    invalid: errors.region
                                                })}/>*/}
                      <span className="text-danger">
                        {errors.region}
                      </span>
                    </div>
                  </div>
                                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">City</label>
                                        </div>
                                    <div className="col-md-9">
                                    <Select
                                                value={city}
                                                onChange={this.handleChange3}
                                                options={this.state.name3}
                                            /> 
                                        
                                 {/*   <input
                                                onChange={this.onChange}
                                                value={this.state.city}
                                                id="city"
                                                type="text"
                                                error={errors.city}
                                                className={classnames("form-control", {
                                                    invalid: errors.city
                                                })}/>*/} 
                      <span className="text-danger">
                        {errors.city}
                      </span>
                    </div>
                  </div>              
                   
                   <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Country</label>
                                        </div>
                                    <div className="col-md-9">
                                       
                                    <input
                                                onChange={this.onChange}
                                                value={this.state.country}
                                                id="country"
                                                type="text"
                                                error={errors.country}
                                                className={classnames("form-control", {
                                                    invalid: errors.country
                                                })}/>
                      <span className="text-danger">
                        {errors.country}
                      </span>
                    </div>
                  </div>
                                     <div className="row mt-2">
                                        <div className="col-md-3">
                                        <label htmlFor="first_currency">Account</label>
                                        </div>
                                    <div className="col-md-9">
                                     {/*  <Select
                                                value={account}
                                                onChange={this.handleChange4}
                                                options={this.state.name4}
                                            />  */}
                                  <input
                                                onChange={this.onChange}
                                                value={this.state.account}
                                                id="account"
                                                type="text"
                                                error={errors.account}
                                                className={classnames("form-control", {
                                                    invalid: errors.account
                                                })}/>
                      <span className="text-danger">
                        {errors.account}
                      </span>
                    </div>
                  </div>
                                      
                                </form>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                <button
                                    form="add-user"
                                    type="submit"
                                    className="btn btn-primary">
                                    Add 
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

UserAddModal.propTypes = {
    addUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};


const mapStateToProps = state => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(
    mapStateToProps,
    { addUser }
)(withRouter(UserAddModal));
